.header {

  &__top-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 28px;

    @include onDesktop {
      margin-top: 12px;
    }
  }

  &__options {
    display: none;

    @include onTablet {
      display: flex;
      align-items: center;
      gap: 28px;
    }
  }

  &__option {
    color: $link-main;

    &--active {
      color: $title-color;
    }
  }

  &__icon {
    display: block;
    width: 24px;
    height: 18px;
    background-image: url(../images/mobile-menu-icon.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    cursor: pointer;
    z-index: 3;
    transition-duration: $duration;

    &:hover {
      background-image: url(../images/menu-hovered.svg);
    }

    @include onTablet {
      display: none;
    }
  }

  &__toggler {
    display: none;

    &:checked ~ .header__icon--close {
      background-image: url(../images/close.png);
    }

    &:checked ~ .mobile-nav {
      display: block;
      right: 0;
    }
  }
}
